import React from 'react'
import Link from 'gatsby-link'

const Tags = ({ pathContext }) => {
  const { posts, tagName } = pathContext

  if (posts) {
    return (
      <div >
        <span className='avenir'>
          Posts about {tagName}:
        </span>
        <ul>
          {posts.map(post => {
             return (
              <li className='avenir'>
                <Link className='dark-gray' to={post.frontmatter.path}>
                  {post.frontmatter.title}
                </Link>
              </li>
             )
          })}
        </ul>
      </div>
    )
  }
}

export default Tags




